import { redirect, type MetaFunction, ActionFunctionArgs, LoaderFunctionArgs, json } from '@remix-run/cloudflare'
import ShieldLogo from 'app/components/logos/ShieldLogo'
import { createUserSession, getUser, login } from 'app/utils/authentication/session.server'

export const meta: MetaFunction = () => {
  return [
    { title: 'Web3Firewall - Login' },
    { name: 'description', content: 'Welcome to Web3Firewall!' },
  ]
}

export const loader = async ({ request, context }: LoaderFunctionArgs) => {
  const user = await getUser(request, context)
  if (user) {
    return redirect('/')
  }

  return json({})
}

export const action = async ({ request, context }: ActionFunctionArgs) => {
  const formData = await request.formData()
  const username = formData.get('username')?.toString() || ''
  const password = formData.get('password')?.toString() || ''

  const user = await login({ username, password, context })
  if (!user) {
    return json({message: 'User not found or password incorrect.'}, 401)
  }

  return createUserSession({ request, token: user.token })
}

